<template>
  <div>
    <LoHeader class="lo_header" title="个人中心"></LoHeader>
    <div class="lo_content_box" >
    <mu-container  class="lo_content">
      <mu-list>
      <mu-list-item avatar button :ripple="false">
        <mu-list-item-action>
          <mu-avatar>
            <img v-if="sysUserInfo.PortraitUrl" :src="sysUserInfo.PortraitUrl">
            <img v-else  src="../../assets/img/avatar/avatar01.jpg">
          </mu-avatar>
        </mu-list-item-action>
        <mu-list-item-title> {{ sysUserInfo.UserName }}</mu-list-item-title>
      </mu-list-item>
      <mu-divider></mu-divider>

      <mu-list-item button :ripple="true" @click="doNickName">
        <mu-list-item-title>昵称</mu-list-item-title>
        {{ sysUserInfo.NickName }}
        <mu-list-item-action>
          <mu-icon size="24" value="check_circle"></mu-icon>
        </mu-list-item-action>
      </mu-list-item>
      <mu-divider></mu-divider>

      <mu-list-item button :ripple="true" @click="doMobile">
        <mu-list-item-title>手机</mu-list-item-title>
        {{ sysUserInfo.Mobile }}
        <mu-list-item-action>
          <mu-icon size="24" value="check_circle"></mu-icon>
        </mu-list-item-action>
      </mu-list-item>
      <mu-divider></mu-divider>

      <mu-list-item button :ripple="true" @click="doEmail">
        <mu-list-item-title>Email</mu-list-item-title>
        {{ sysUserInfo.Email }}
        <mu-list-item-action>
          <mu-icon size="24" value="check_circle"></mu-icon>
        </mu-list-item-action>
      </mu-list-item>
      <mu-divider></mu-divider>

      <mu-list-item button :ripple="true" @click="openModifyPwdAlert">
        <mu-list-item-title>密码</mu-list-item-title>
        ******
        <mu-list-item-action>
          <mu-icon size="24" value="check_circle"></mu-icon>
        </mu-list-item-action>
      </mu-list-item>
      <mu-divider></mu-divider>
    </mu-list>
    </mu-container>

      <mu-dialog title="修改密码" width="600" max-width="80%" :esc-press-close="false" :overlay-close="false" :open.sync="showModifyPwdAlert">
        <mu-text-field type="password" v-model="oldPwd" placeholder="原密码"></mu-text-field><br/>
        <mu-text-field type="password" v-model="newPwd1" placeholder="新密码"></mu-text-field><br/>
        <mu-text-field type="password" v-model="newPwd2" placeholder="再次确认新密码"></mu-text-field><br/>
        <mu-button slot="actions" flat color="primary" @click="showModifyPwdAlert=false">取消</mu-button>
        <mu-button slot="actions" flat color="primary" @click="doModifyPwd">确定</mu-button>
      </mu-dialog>

      <mu-dialog :title="OtherAlertTitle" width="600" max-width="80%" :esc-press-close="false" :overlay-close="false" :open.sync="showModifyOtherAlert">
        {{OtherAlertHint}}
        <mu-text-field type="text" v-model="FieldValue" placeholder=""></mu-text-field><br/>
        <mu-button slot="actions" flat color="primary" @click="showModifyOtherAlert=false">取消</mu-button>
        <mu-button slot="actions" flat color="primary" @click="doModifyOther">确定</mu-button>
      </mu-dialog>
    </div>
  </div>
</template>

<script>
import LoHeader from '../../components/LoHeader.vue'
import { mapState } from 'vuex'
import {ModifyPwd,ModifySysUserInfo} from '../../api/sys';
export default {
  name: "MyCenter",
  components: {
    LoHeader
  },
  data() {
    return {
      showModifyPwdAlert:false,
      oldPwd:'',
      newPwd1:'',
      newPwd2:'',

      FieldName:'',
      FieldValue:'',
      OtherAlertTitle:'昵称',
      OtherAlertHint:'请输入新的昵称',
      showModifyOtherAlert:false,
    }
  },
  computed: {
    ...mapState({
      sysUserInfo: state => state.sysUser.sysUserInfo
    })
  },
  methods: {
    openModifyPwdAlert(){
      this.oldPwd = '';
      this.newPwd1= '';
      this.newPwd2= '';
      this.showModifyPwdAlert = true;
    },
    doNickName(){
      this.FieldName = 'NickName';
      this.showModifyOtherAlert = true;
      this.OtherAlertHint= '请输入新的昵称';
      this.OtherAlertTitle = '修改昵称'
    },
    doMobile(){
      this.FieldName = 'Mobile';
      this.showModifyOtherAlert = true;
      this.OtherAlertHint= '请输入新的手机';
      this.OtherAlertTitle = '修改手机'
    },
    doEmail(){
      this.FieldName = 'Email';
      this.showModifyOtherAlert = true;
      this.OtherAlertHint= '请输入新的Email';
      this.OtherAlertTitle = '修改Email'
    },
    doModifyOther(){
      ModifySysUserInfo({FieldName:this.FieldName,FieldValue:this.FieldValue}).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {
          this.$toast.info('修改成功.');
          this.showModifyOtherAlert = false;

          if (this.FieldName== 'Email') {
            this.sysUserInfo.Email = this.FieldValue;
          } else if (this.FieldName== 'Mobile') {
            this.sysUserInfo.Mobile = this.FieldValue;
          }else if (this.FieldName== 'NickName') {
            this.sysUserInfo.NickName = this.FieldValue;
          }

          resolve();
        } else {
          this.$toast.error(msg);
        }
      }).catch(error => {
        // this.$toast.error('下分失败,原因:'+error);
      });
    },

    doModifyPwd(){
      if (this.newPwd1!=this.newPwd2) {
        this.$toast.error('两次新密码不一致.');
        return
      }
      ModifyPwd({OldPwd:this.oldPwd,NewPwd:this.newPwd1}).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {
          this.$toast.info('修改成功.');
          this.showModifyPwdAlert = false;
          resolve();
        } else {
          this.$toast.error(msg);
        }
      }).catch(error => {
        // this.$toast.error('下分失败,原因:'+error);
      });


    }

  }
}
</script>

<style scoped>
@import url('../../assets/css/main.css');

</style>



