import { request } from '../utils/http';

export function getMenu(data) {
  return  request({ url:'sys/getmenulist', method:'post', data:data })
}

export function ModifyPwd(data) {
  return  request({ url:'sys/modifypwd', method:'post', data:data })
}

export function ModifySysUserInfo(data) {
  return  request({ url:'sys/modifysysuserinfo', method:'post', data:data })
}



